import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'


Vue.use(Router)

import Content from '@/components/content'
import Wrapper from '@/components/wrapper'

export default new Router({
  routes: [
    {path:"/",redirect:"/content"},
    {
      path: '/content',
      name: 'content',
      components: {
      	wrap:Wrapper
      }
    }
    
  ]
})



